.App {
  display: flex;
  flex-wrap: nowrap;
  padding: 10px 10px 0 10px;
}

.components-list {
  width: 18vw;
  margin-right: 2vw;
  padding: 4vh 2vw 0 2vw;
  height: 95vh;
}

.drop-area {
  width: 80vw;
  height: 95vh;
  border: 1px black dashed;
  border-radius: 1vw;
  font-size: 64px;
  text-align: center;
  line-height: 95vh;
  color: gray;
  user-select: none;
}

.component {
  cursor: pointer;
  width: 12vw;
  text-align: center;
  font-size: 20px;
  line-height: 10vh;
  background: white;
}

.components-list .component {
  margin-bottom: 4vh;
}

.blue {
  color: blue;
  border: 2px solid blue;
  border-radius: 10px;
}

.green {
  color: green;
  border: 2px solid green;
  border-radius: 10px;
}

.purple {
  color: purple;
  border: 2px solid purple;
  border-radius: 10px;
}
